html, body, #root {
  background: #f2f2f2;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Goudy Bookletter 1911", sans-serif;
}

.app {
  flex-direction: column;
  height: 100%;
  display: flex;
}

header {
  background: #fff;
  flex: none;
  justify-content: space-between;
  align-items: center;
  padding: 2px 16px;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

header h1 {
  text-align: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin: 0 !important;
}

header h2 {
  letter-spacing: 1.25px;
  margin: 0;
  font-size: 1.4rem;
}

.logo {
  height: 48px;
  margin-right: 15px;
  padding: 10px;
}

.refresh-btn {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.8rem;
}

main {
  flex: auto;
  padding: 16px;
  overflow-y: auto;
}

.toolbar {
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
  display: flex;
}

.name-label {
  font-size: 1rem;
  font-weight: 600;
}

.name-select {
  width: 360px;
}

.name-select .react-select__control {
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: .95rem;
}

.name-select .react-select__menu {
  z-index: 20;
}

.grid {
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 16px;
  display: grid;
}

.card {
  text-align: center;
  background: #fff;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  padding: 22px;
  display: flex;
  box-shadow: 0 1px 3px #0000001a;
}

.icon-wrap {
  background: #fafafa;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 90px;
  height: 90px;
  margin-bottom: 8px;
  display: flex;
}

.icon-wrap img {
  max-width: 65px;
  max-height: 65px;
}

.title {
  margin: 4px 0;
  font-weight: 600;
}

.price {
  color: #666;
  margin-bottom: 28px;
}

.card input {
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 48px;
  padding: 4px;
}

footer {
  background: #fff;
  flex: none;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  display: flex;
  box-shadow: 0 -2px 4px #0000001a;
}

.summary {
  font-size: 1.1rem;
  font-weight: bold;
}

.submit-btn {
  color: #fff;
  cursor: pointer;
  background: #1ea0ca;
  border: none;
  border-radius: 6px;
  padding: 10px 20px;
  font-size: 1rem;
}

.submit-btn:disabled {
  opacity: .6;
  cursor: default;
}

.qty-control {
  align-items: center;
  gap: 12px;
  margin-top: auto;
  display: flex;
}

.qty-btn {
  cursor: pointer;
  background: #e0e0e0;
  border: none;
  border-radius: 6px;
  width: 32px;
  height: 32px;
  padding: 0;
  font-size: 1.2rem;
  line-height: 1;
}

.qty-btn:disabled {
  opacity: .4;
  cursor: default;
}

.qty-value {
  text-align: center;
  min-width: 24px;
  font-weight: 550;
}

.Toastify__toast--success, .Toastify__toast--error {
  background: #fff;
}

.Toastify__toast {
  border-radius: 8px;
  font-size: .95rem;
}

.Toastify__progress-bar {
  background: #fff;
}

@media (hover: none) {
  .qty-btn {
    width: 40px;
    height: 40px;
    font-size: 1.4rem;
  }
}
